<template>
  <div class="wrap"
       v-loading="!params.data.length"
       :element-loading-background="$STANDARD.loadingBgColor"
  >
    <div ref="chartEl" :style="{width:params.canvasStyle.width,height:params.canvasStyle.height}"></div>
    <div class="circle" v-if="params.data.length" :style="{
              left:params.options.center[0],
              top:params.options.center[1],
               width:params.options.bgSize+'px',
              height:params.options.bgSize+'px'
            }"></div>
  </div>

</template>

<script>
  import * as echarts from "echarts";

  export default {
    name: "ring",
    props: {
      params: Object
    },
    data() {
      return {
        myChart: null
      };
    },
    mounted() {
      this.init();
    },
    methods: {
      init() {
        if (this.myChart) {
          this.myChart.dispose();
          this.myChart = null;
        }
        if (this.params.data.length === 0) return;

        this.myChart = echarts.init(this.$refs.chartEl);
        let options = this.params.options;
        let data = JSON.parse(JSON.stringify(this.params.data));
        let percent = (data[0].percent * 100).toFixed(2);

        function getData() {
          return [{
            value: percent,
            itemStyle: {
              normal: {
                borderWidth: 22,
                color: {
                  colorStops: [
                    {
                      offset: 0,
                      color: options.colors[0][0] // 0% 处的颜色
                    },
                    {
                      offset: 1,
                      color: options.colors[0][1]  // 100% 处的颜色
                    }
                  ],
                  globalCoord: false // 缺省为 false
                }
              }
            }
          }, {
            value: 100 - percent,
            itemStyle: {
              normal: {
                color: 'transparent'
              }
            }
          }];
        }

        this.myChart.setOption({
          title: {
            text: this.params.data[0].value + '\n' + this.params.data[0].name + '\n' + percent + '%',
            x: 'center',
            y: 'center',
            textStyle: {
              color: '#f1b178',
              fontWeight: 'bold',
              lineHeight: 32,
              fontSize: 18,
            }
          },
          series: [
            {
              type: 'pie',
              radius: options.bgRadius,
              center: options.center,
              silent: true,
              label: {
                normal: {
                  show: false,
                }
              },
              data: [
                {
                  value: 1,
                  itemStyle: {
                    normal: {
                      color: '#fff',
                    }
                  }
                }
              ],
              animation: false
            },
            {
              name: 'main',
              type: 'pie',
              radius: options.radius,
              center: options.center,
              label: {
                normal: {
                  show: false,
                }
              },
              data: getData(),
              animationEasingUpdate: 'cubicInOut',
              animationDurationUpdate: 500
            }
          ]
        })
      }
    },
    watch: {
      'params.data': {
        handler(newVal, oldVal) {
          this.init();
        },
        deep: true
      }
    },
  }
</script>

<style lang="stylus" scoped>
  .wrap
    position relative
    width 100%
    height 100%
    .circle
      position absolute
      background-color rgba(255, 255, 255, 0.1)
      border-radius 50%
      z-index -1
      transform translate(-50%, -50%)
</style>
