<template>
  <div class="wrapper">
    <div class="content">
      <div class="w-left">
        <ctitle :title="'区域信访量动态'"></ctitle>
        <ul class="barType clearfloat">
          <li
            v-for="(item, index) in barTypeList"
            :key="index"
            :class="curBarType === item ? 'active' : ''"
            @click="changeBarType(item)"
          >{{item}}
          </li>
        </ul>
        <div class="echarts">
          <horizontal-bar :params="leftBarParams"></horizontal-bar>
        </div>
      </div>
      <div class="w-right">
        <div class="w-top">
          <div class="wt-t-item">
            <ctitle :title="'来信信访量信访初信重信联名信占比'"></ctitle>
            <allCount class="all-count" :count="allcount"></allCount>
            <div class="inner">
              <div class="detail">
                <detailList :params="detailList"></detailList>
              </div>
              <div class="chart">
                <loop-pie :params="zbPieParams"></loop-pie>
              </div>
              <div class="chart">
                <ring :params="zbRingParams"></ring>
              </div>
            </div>
          </div>
          <div class="wt-t-item">
            <ctitle :title="'来信信访量信访四级登记机构占比'"></ctitle>
            <div class="inner">
              <div class="detail">
                <detailList :params="jgDetailList"></detailList>
              </div>
              <div class="chart">
                <loopPie :params="jgzbPieParams"></loopPie>
              </div>
            </div>
          </div>
        </div>
        <div class="r-bottom">
          <div class="r-b-l">
            <ctitle :title="'初信化解率'"></ctitle>
            <div class="inner">
              <cir-circle :count="hjvValue"></cir-circle>
            </div>
          </div>
          <div class="r-b-r">
            <ctitle :title="'来信信访量初信重信走势'"></ctitle>
            <div class="inner">
              <LinesR :params="zsLineParams"></LinesR>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import {gradientColor} from "@/utils/gradient";
  import ctitle from "@/components/title/index";
  import incNumber from "@/components/incNumber/index";
  import barline from "@/components/bar/BarLine";
  import loopPie from "@/components/pie/LoopPie";
  import ring from "@/components/pie/ring";
  import PieChart3D from "@/components/pie/PieChart3D";
  import LinesR from "@/components/line/LinesR";
  import horizontalBar from "@/components/bar/HorizontalBar";
  import allCount from "@/components/list/allCount";
  import detailList from "@/components/list/detailList";
  import cirCircle from "@/components/cirCircle/index";

  export default {
    name: "lxfx",
    components: {
      ctitle,
      incNumber,
      barline,
      loopPie,
      PieChart3D,
      ring,
      LinesR,
      horizontalBar,
      allCount,
      detailList,
      cirCircle
    },
    data() {
      return {
        curBarType: "件次",
        barTypeList: ["件次", "人次"],
        // 左侧柱图
        leftBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          }
        },
        // 初信重信联名信占比
        allcount: -1,
        detailList: null,
        zbPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            bgSize: 220,
            colors:this.$STANDARD.gradientColors,
            radius: ["45%", "55%"],
            center: ["50%", "50%"],
            centerRadius: ["40%", "41%"],
            labelRich: {
              b: this.fontSize(0.18),
              d: this.fontSize(0.2),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 70,
            // borderColor: "#050f28",
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "16%" //文字大小
            },
            labelSize: "24%", // label字号
            rollFlag: true,
            threeLevel: true,
            colorsArr: ["#13f0ff", "#4eed99", "#d93e75", "#ccbe51", "#eb9002", "#1ba1ff", "#563ad2", "#f89e67"]
          }
        },
        zbRingParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            bgSize: 180,
            radius: ['54%', '65%'],
            bgRadius: ['59%', '60%'],
            center: ["50%", "50%"],
            colors: [
              ["#fee89f", "#eea570"]
            ]
          }
        },
        // 四级登记机构占比
        jgDetailList: null,
        jgzbPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            colors: this.$STANDARD.gradientColors,
            radius: ["45%", "60%"],
            center: ["50%", "50%"],
            centerRadius: ["50%", "51%"],
            labelRich: {
              b: this.fontSize(0.2),
              d: this.fontSize(0.2),
              bHeight: this.fontSize(0.3)
            },
            borderWidth: 5,
            length: 20,
            length2: 45,
            bRichPadding: [30, -45, 0, -45],
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "16%" //文字大小
            },
            labelSize: "22%", // label字号
            rollFlag: true
          }
        },
        // 化解率
        hjvValue: -1,
        // 初信重信走势
        zsLineParams: {
          data: [],
          colors: [
            {
              line: "#e11770",
              start: "rgba(225, 23, 112, .5)",
              end: "rgba(225, 23, 112, .5)"
            },
            {
              line: "#ffb435",
              start: "rgba(255, 180, 53, .5)",
              end: "rgba(255, 180, 53, .5)"
            },
            {
              line: "#1a82f7",
              start: "rgba(26, 130, 247, .5)",
              end: "rgba(26, 130, 247, .5)"
            },
            {
              line: "#a1d871",
              start: "rgba(161, 216, 113, .5)",
              end: "rgba(161, 216, 113, .5)"
            },
            {
              line: "#ff825b",
              start: "rgba(255, 130, 91, .5)",
              end: "rgba(255, 130, 91, .5)"
            }
          ],
          option: {
            splitNumber: 4,
            axisLabelColor: "#fff",
            yaxisLabelColor: "#82d8ff",
            legendfontSize: 18,
            splitLineShow: true,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisYName: "件次",
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: true,
            areaShow: 0,
            smoothMonotone: "none",
            dataZoomshow: false,
            left: "5%",
            right: "8%",
            top: "20%",
            bottom: "5%",
            legendTop: "0%",
            legendRight: "8%",
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {},
          xName: "name"
        },
      };
    },
    mounted() {
      this.getAPI(this.date);
    },
    methods: {
      getAPI(date) {
        // 1区域信访量动态（左侧柱图）
        this.getLeftAPI(date);
        // 2.初信重信联名信占比（总数）
        get_http("xfxsLxZtZbTotal", {day: `[${date.startDate},${date.endDate}]`})
          .then(res => {
            this.allcount = res.data.data[0].value;
          })
          .catch(err => {
            console.log(err);
          });
        // 3.初信重信联名信占比（各类型）
        get_http("xfxsLxZtZbType", {day: `[${date.startDate},${date.endDate}]`})
          .then(res => {
            this.detailList = res.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 4.初信重信联名信占比（饼图）
        get_http("xfxsLxZtZbPie", {day: `[${date.startDate},${date.endDate}]`})
          .then(res => {
            this.zbPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 5.初信重信联名信占比（圆环图）
        get_http("xfxsLxZtZbRing", {day: `[${date.startDate},${date.endDate}]`})
          .then(res => {
            let obj = {};
            let valueKey = 0, percentKey = 0;
            for(let key  in res.data.map){
              if(res.data.map[key]==='value'){
                valueKey=key
              }
              if(res.data.map[key]==='precent'){
                percentKey=key
              }
            }
            obj.name = res.data.data[0].name;
            obj.value=res.data.data[0][valueKey];
            obj.percent=res.data.data[0][percentKey];
            this.zbRingParams.data = [obj];
          })
          .catch(err => {
            console.log(err);
          });
        // 6.登记机构占比（各类型）
        get_http("xfxsLxZtDjjgzbType", {day: `[${date.startDate},${date.endDate}]`})
          .then(res => {
            this.jgDetailList = res.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 7.登记机构占比（饼图）
        get_http("xfxsLxZtDjjgzbPie", {day: `[${date.startDate},${date.endDate}]`})
          .then(res => {
            this.jgzbPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 8.初信化解率
        get_http("xfxsLxZtHjv", {day: `[${date.startDate},${date.endDate}]`})
          .then(res => {
            this.hjvValue = (res.data.data[0].value * 100).toFixed(2);
          })
          .catch(err => {
            console.log(err);
          });
        // 9.初信重信走势
        get_http("xfxsLxZtZsLine", {day: `[${date.startDate},${date.endDate}]`})
          .then(res => {
            this.zsLineParams.map = res.data.map;
            this.zsLineParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 1区域信访量动态（左侧柱图）
      getLeftAPI(date) {
        let type = this.curBarType === "件次" ? 'xfjc' : 'xfrc';
        get_http("xfxsLxZtLeftbar", {index: type, day: `[${date.startDate},${date.endDate}]`})
          .then(res => {
            this.leftBarParams.data = res.data.data.sort((a, b) => {return a.value - b.value;});
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 切换左侧人次件次
      changeBarType(type) {
        this.curBarType = type;
        this.getLeftAPI(this.date);
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth =
            window.innerWidth ||
            document.documentElement.clientWidth ||
            document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      }
    },
    computed: {
      date() {
        return this.$store.state.date;
      }
    },
    watch: {
      date(val) {
        this.getAPI(val);
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)
    .content
      width 100%
      height 100%
      display flex
      padding .2rem .3rem
      box-sizing border-box

      .w-left
        width 3rem
        height 100%
        position relative
        font-size 0
        border-right 1px #91e3f7 dashed
        .barType
          position relative
          left 1.7rem
          display inline-block
          z-index 1000
          overflow hidden
          li
            float left
            margin 0.1rem
            color #1d81c9
            font-size 0.16rem
            cursor pointer
          li.active
            color #82d8ff
            font-weight bold
        .echarts
          height 92%
      .w-right
        flex 1
        height 100%
        display flex
        flex-direction column
        box-sizing border-box

        .w-top
          height 4rem
          display flex

          .wt-t-item
            position relative
            height 100%
            .all-count
              position absolute
              top 0
              left 40%
              width 4rem
              height 0.48rem
            &:nth-child(1)
              flex 3
            &:nth-child(2)
              flex 2
            .inner
              display flex
              height calc(100% - 28px)
              justify-content center
              align-items center
              .detail
                flex 1
                height 100%
              .chart
                width 100%
                height 100%
                flex 1
        .r-bottom
          flex 1
          width 100%
          height 100%
          display flex
          .inner
            position relative
            width 100%
            height calc(100% - 0.28rem)
          .r-b-l
            flex 1
          .r-b-r
            flex 4
            position relative


</style>
